<template>
  <div id="app" :style="bg">
    <nav class="navbar navbar-default navbar-fixed-top" :style="color">
      <h3>
        <marquee width=100% behavior=alternate direction=left align=middle>欢迎光临王绍军的个人博客</marquee>
      </h3>
    </nav>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  data(){
    return{
      bg:{backgroundImage:'url('+require('../static/bg2.jpg')+')'},
      color:{backgroundColor:'#36536C'},
    }
  }
}
</script>
<style>
  html,body{
    height: 100%;
    margin-top: 30px;
  }
  #app{
    width:100%;
    height: 100%; 
    background-size: 100% 100%;
    background-repeat: no-repeat;
  }
  .navbar-default{
    background-color:#36536C;
    color: white;
    box-shadow: 0px 1px 1px #839eb3;
  }
</style>



