<template>
  <div class="container-fluid">
    <div class="snow-container">
      <canvas id="mycanvas"></canvas>
    </div>
    <div class="yy">
      <h2>明天的世界更加美好！</h2>
      <h3>加油全世界</h3>
      <h4>加油我自己</h4>
      <h5>----2017.7.6</h5>
    </div> 
  </div>
</template>
<script>
export default {
  data () {
    return {
      
    }
  },
  mounted(){
   function rnd(n, m){
      return Math.floor(Math.random()*(m-n) + n);
    };

  function d2a(n){
    return n*Math.PI/180;
  };

  //在画布中生成雪花：
  var oC = document.getElementById("mycanvas");
  var h =$('#app').height();
  var w = $('#app').width();
  oC.setAttribute('width', w);
  oC.setAttribute('height', h);
  var gd = oC.getContext("2d");
  var maxW = w;
  var maxH =h;
  var arr = [];
  var width = 20; 

  for(var i=0; i<100; i++){
    arr.push({
        "left":rnd(0, maxW),
        "top":rnd(0, maxH),
        "deg":rnd(-10, 10),
        "scale":rnd(0, 10) 
        });
  }

  setInterval(function(){
    gd.clearRect(0, 0, oC.width, oC.height);
    gd.save();

    for(var i=0; i<arr.length; i++){
      var h = 0.5 * arr[i].scale;
      arr[i].left = arr[i].left + Math.tan(d2a(arr[i].deg))*h;
      arr[i].top = arr[i].top + h;

      //若已在画面外则删除
      if(arr[i].left < 0 || arr[i].left > maxW || arr[i].top > maxH){
        arr.splice(i--, 1);
        continue;
      }

      var width_i = arr[i].scale;
      var ra = gd.createRadialGradient(arr[i].left, arr[i].top, width_i/4, arr[i].left, arr[i].top, width_i);
      ra.addColorStop(0, "rgba(255,255,255,1)");
      ra.addColorStop(1, "rgba(255,255,255,0.1)");
      gd.fillStyle = ra;

      gd.beginPath();
      gd.arc(arr[i].left, arr[i].top, width_i, 0, 2*Math.PI);
      gd.fill();
    }

    gd.restore();
  }, 16);

  //不断增加新的雪花
  function next(){
    setTimeout(function(){
      if(arr.length < 200){
        for(var i=0; i<20; i++){
          arr.push({
              "left":rnd(0, w),
              "top":0,
              "deg":rnd(-10, 10),
              "scale":rnd(2, 10) 
              });
        }
      }
      next();
    }, Math.random()*200+500);
  };

  next();
  }
}
</script>

<style scoped>
.snow-container {
   position: fixed;
   top: 0; left: 0;
   width: 100%; 
   height: 100%;
   pointer-events: none; 
   z-index: 100001; 
 }
 .yy{
  color: #681818;
  position: relative;
  width:300px;
  margin-left: -150px; 
  left: 50%;
  top: 200px; 
 }
 h5{
  text-align: right;
 }
</style>



